<template>
<div id="app">
    <van-tabs v-model="active" class="nav-bar-top">
        <van-tab :title="$t('message.home') "></van-tab>
        <van-tab :title="$t('message.income')" ></van-tab>
        <van-tab :title="$t('message.margin')" ></van-tab>
        <van-tab :title="$t('message.cost') "></van-tab>
        <van-tab :title="$t('message.grossMargin') "></van-tab>
    </van-tabs >
    <!-- <van-nav-bar class="nav-bar-top" :title="$t(`message.${pageMap[active]}`)" left-text="" right-text="todo" left-arrow @click-left="onClickLeft"/> -->
    <!-- <van-switch v-model="lang" active-color="#07c160" inactive-color="#f44" size="15px" class="lang-switch"/> -->
    <router-view class="view" />
    <!-- <van-tabbar v-model="active" class="nav-bar-bottom">
        <van-tabbar-item icon="home-o">{{ $t('message.home') }}</van-tabbar-item>
        <van-tabbar-item icon="balance-o">{{ $t('message.income') }}</van-tabbar-item>
        <van-tabbar-item icon="balance-list-o">{{ $t('message.margin') }}</van-tabbar-item>
        <van-tabbar-item icon="notes-o">{{ $t('message.cost') }}</van-tabbar-item>
        <van-tabbar-item icon="cash-back-record">{{ $t('message.grossMargin') }}</van-tabbar-item>
    </van-tabbar> -->
    <button @click="handleClick">获取数据</button>
</div>
</template>

<script>
import { NavBar,Tab, Tabs,Switch} from 'vant';
import Cube from './tools/cube';
var cube = new Cube();

export default {
    name: 'App',
    components:{
      [NavBar.name]: NavBar,
      [Tab.name]: Tab,
      [Tabs.name]: Tabs,
      [Switch.name]: Switch
    },
    data() {
        return {
            active: 1,
            lang: true,
            pageMap: {
                0: 'overall',
                1: 'income',
                2: 'margin',
                3: 'cost',
                4: 'grossMargin'
            },
            pageTitleMap: {
                0: '总体情况',
                1: '工作类型',
                2: '拜访统计',
                3: '计划执行',
                4: '拜访预警'
            }
        }
    },
    mounted() {
        /**
         * qApp: sense app 对象
         * this: vue 对象
         * rankByCustomer: 对象config中KPI公式
         * page2: store中参数名称
         */
        cube.getData(qApp, this, 'rankByCustomer', 'customerData');
    },
    methods: {
        onClickLeft() {
            console.log('onClickLeft');
        },
        handleClick() {
            console.log('handleClick:');
            cube.getData(qApp, this, 'rankByCustomer', 'customerData');
        }
    },
    watch: {
        active(pIndex) {
            console.log('pIndex: ', pIndex);
            this.$router.push(this.pageMap[pIndex]);
        },
        lang(lang) {
            if(!lang) {
                this.$i18n.locale='zhCHS';
                this.$Local('zhCHS');
            }
            else {
                this.$i18n.locale='en';
                this.$Local('en');
            }
        }
    }
}
</script>

<style>
@import './assets/css/common.css';

#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    text-align: center;
    color: #2c3e50;
}

html,
body,
#app {
    height: 100vh;
    width: 100%;
    overflow: hidden !important;
}

.lang-switch {
    position: absolute;
    top: 20px;
    right: 80px;
    z-index: 10;
    display: none;
}

.view {
    height: calc(100% - 70px);
    overflow-y: auto;
}

.nav-bar-top {
    height: 60px;
    line-height: 60px;
}

.van-tabs__nav .van-ellipsis {
    font-size: 20px;
}


</style>
